<template>
  <div id="agreements">
    <div class="nav">
      <span class="first" @click="goIndex">协议规则</span
      ><span>{{ second }}</span>
    </div>
    <div class="protocals">
      <div class="protocal-items">
        <div v-html="html" class="wordRoot"></div>

        <!-- <InnerHtml :htmml="html" /> -->
      </div>
    </div>
  </div>
</template>

<script>
import { htmls, config } from "../assets/commonPro.config";
import { setQuery } from "@/pages/internal/common/query";
export default {
  name: "test-1",
  props: ["editable", "url", "asComponent"],
  data() {
    return {
      html: "",
      second: "",
    };
  },
  created() {
    console.log(config);
  },
  components: {},
  methods: {
    showProtocals() {
      this.setSecond();

      if (typeof this.asComponent == "string") {
        console.log("show protocal asComponent: ", this.asComponent);
        this.html = htmls[this.asComponent];
      } else {
        const key = this.$route.params.name;

        this.html = htmls[key];
      }
      console.log(this.html);
    },
    goIndex() {
      this.html = htmls["abcabc"];
      setQuery({ AgreementPage: 0 });
      this.setSecond();
    },
    getQueryParam(key) {
      const reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)", "i");
      const r = window.location.search.substr(1).match(reg);
      console.log(r);
      if (r != null) {
        return decodeURI(r[2]);
      }
      return null;
    },
    setSecond() {
      let key = Number(this.getQueryParam("AgreementPage"));
      if (key === 0) {
        this.second = "";
      } else if (key === 1) {
        this.second = " > 工作台指引";
      } else if (2 <= key && key <= 8) {
        this.second = " > 入驻协议";
      } else if (9 <= key && key <= 16) {
        this.second = " > 日常管理";
      } else {
        this.second = " > 保险";
      }
    },
  },
  watch: {
    url() {
      this.showProtocals();
    },
    asComponent() {
      this.showProtocals();
    },
  },
  mounted() {
    window.addEventListener("load", this.showProtocals());
  },
};
</script>
