// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import {createApp} from 'vue'
//import App from './App'
//import Home from './Home.vue'
import router from './router'
import agreements from './agreements.vue'
import axios from 'axios'

//import '../../assets/css/font.css'
const app=createApp(agreements).use(router);
app.config.compilerOptions.isCustomElement = tag => tag.includes('go-');
app.provide('$set',function(obj,key,val){
    obj[key]=val;
  })
  .provide("axios",axios)
  .mixin({inject:["$set","axios"]})
.mount("#test");
console.log("agr main js: ");
