import { createRouter, createWebHistory } from "vue-router";
import aboutus from "./components/aboutus";
import common from "./components/commonPro.vue";
// import verifiedRoutes from './assets/common.config'
import { verifiedRoutes } from "./assets/commonPro.config.js";
const routes = [
  {
    path: "/aboutus",
    name: "aboutus",
    component: aboutus,
    //这个页面和其它9个不同单独放置
    meta: {
      title: "关于我们",
    },
  },
  {
    path: "/:name",
    name: "allAgreements",
    component: common,
    beforeEnter: (to, from, next) => {
      if (to.params.name in verifiedRoutes) {
        next();
      } else {
        next("/e0a219d9884d27910161db8b8a6ef1d6");
        //进行路由拦截，默认跳转到service路由
      }
    },
  },
  {
    path: "/",
    component: common,
    beforeEnter: (to, from, next) => {
      next("/e0a219d9884d27910161db8b8a6ef1d6");
    },
  },
];

const router = createRouter({
  history: createWebHistory("/agreements.html"),
  routes,
});
router.beforeEach((to, from, next) => {
  document.title =
    to.matched[0]?.meta.title || to.matched[1]?.meta.title || "营探camptogo";
  next();

  // cookieOperations
  /* if (vm.cookieOperations.getCookie('authToken')) {
    if (to.name !== 'Login') {
      next();
    }
    else {
      next({
        path: '/tools/provider',
      })
    }
  }
  else {
    if (to.name === 'Login') {
      next();
    }
    else {
      if(to.matched.some((record) => record.meta.auth)) {
        next({
          path: '/tools/login',
        });
      }
      else {
        next();
      }
    }
  } */
  /* if (window.localStorage.getItem('authToken') && window.localStorage.getItem('authToken') !== '') {
    if (to.name !== 'Login') {
      next();
    }
    else {
      next({
        path: '/tools/provider',
      })
    }
  }
  else {
    if (to.name === 'Login') {
      next();
    }
    else {
      if(to.matched.some((record) => record.meta.auth)) {
        next({
          path: '/tools/login',
        });
      }
      else {
        next();
      }
    }
  } */
});
export default router;
